:root {
  --rain-bg-color: #2d3436;
  --rain-grad-first: #2d3436;
  --rain-grad-second: #ecf0f1;
  --text-cursor-color: black;
  --highlight-color: #cccccc;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100vh;
}

.rain-canvas {
  background-color: var(--rain-bg-color);
  background-image: linear-gradient(to bottom, var(--rain-grad-first), var(--rain-grad-second));
}

#root, #root > div {
  height: 100vh;
}

canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -999;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.text-cursor {
  position: relative;
  width: fit-content;
}

.text-cursor:after {
  position: absolute;
  top: 0;
  left: 100%;
  height: 100%;
  content: ' ';
  padding-left: 1px;
  border-right: 1px solid var(--text-cursor-color);
  animation: cursor-blink 1s linear infinite;
}

.highlighted {
  background-color: var(--highlight-color);
  color: white;
}

@keyframes cursor-blink {
  0%, 100% {
    border-right-color: var(--text-cursor-color);
  }

  50% {
    border-right-color: transparent;
  }
}